import { useState } from 'react';
import { addContributionReminderCookie, ReminderStatus, } from '../lib/reminders';
const CREATE_ONE_OFF_REMINDER_ENDPOINT = 'https://support.theguardian.com/reminders/create/one-off';
export function useContributionsReminderSignup(reminderPeriod, reminderPlatform, reminderComponent, reminderStage, reminderOption) {
    const [reminderStatus, setReminderStatus] = useState(ReminderStatus.Editing);
    const createReminder = (email) => {
        const reminderSignupData = {
            email,
            reminderPeriod,
            reminderPlatform,
            reminderComponent,
            reminderStage,
            reminderOption,
        };
        setReminderStatus(ReminderStatus.Submitting);
        fetch(CREATE_ONE_OFF_REMINDER_ENDPOINT, {
            body: JSON.stringify(reminderSignupData),
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
        })
            .then((response) => {
            if (!response.ok) {
                setReminderStatus(ReminderStatus.Error);
            }
            else {
                setReminderStatus(ReminderStatus.Completed);
                addContributionReminderCookie(reminderPeriod);
            }
        })
            .catch(() => setReminderStatus(ReminderStatus.Error));
    };
    return { reminderStatus, createReminder };
}
