// --- Cookie utils --- //
const dateDiff = (start, end) => {
    const twentyFourHours = 86400000;
    return Math.round((end.valueOf() - start.valueOf()) / twentyFourHours);
};
export const addContributionReminderCookie = (reminderDateString) => {
    const today = new Date();
    const reminderDate = new Date(Date.parse(reminderDateString));
    setCookie({
        name: 'gu_epic_contribution_reminder',
        value: '1',
        daysToLive: dateDiff(today, reminderDate),
    });
};
export const hasSetReminder = () => {
    return !!getCookie({ name: 'gu_epic_contribution_reminder' });
};
// --- Text utils --- //
const PREPOSITION_REGEX = /^(on|in)/;
const containsPreposition = (text) => PREPOSITION_REGEX.test(text);
const addPreposition = (text) => 'in ' + text;
export const ensureHasPreposition = (text) => containsPreposition(text) ? text : addPreposition(text);
// --- Validation utils --- //
export const isValidEmail = (email) => {
    const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
};
export const emailIsShortEnoughForIdentity = (email) => {
    // Identity errors on email addresses longer than 100 characters
    return email.length <= 100;
};
